<template>
    <div> 
        <div class="talk-text">
            <div class="talk-list" v-for="item in chatList" :key="item.answer">
                <div class="qu">{{item.question}}</div>
                <div class="as">{{item.answer}}</div>
            </div>
        </div>
        <div class="talk-form">
            <input type="text" v-model="tInfo">
            <button @click="sendMsg">send</button>
        </div>
    </div>
</template>
<script>
import md5 from "blueimp-md5"
export default {
    data() {
        return {
            tInfo:"",
            chatList:[]
        }
    },
    created() {
        let obj = {
            title:"聊天",
            navName:"chat"
        }
        this.$emit("changeActive",obj)
    },
    methods: {
        getTimeStamp(){
            let timer = new Date();
            timer = Date.parse(timer);
            timer = timer.toString().substr(0,10);
            return timer;
        },
        getNonceStr(){
            let str = Math.random().toString(36);
            return str.substr(2);
        },
        getSign(params){
            //1.key值升序
            let arr = Object.keys(params).sort();
            //2.url转码
            let str = "";
            for(let i = 0;i<arr.length;i++){
                str += arr[i]+"="+encodeURI(params[arr[i]])+"&"
            }
            //3.拼接
            str += "app_key=zyS2mSZrO0qAWjzT";
            //4.md5加密
            str = md5(str).toString().toUpperCase();

            return str;
        },
        sendMsg(){
            let birdapi = 'https://bird.ioliu.cn/v2?url=';
            let urlapi = "https://api.ai.qq.com/fcgi-bin/nlp/nlp_textchat";
            let params={
                app_id:"2119597165",
                time_stamp:this.getTimeStamp(),
                nonce_str:this.getNonceStr(),
                session:"10000",
                question:this.tInfo
            }
            
            params.sign = this.getSign(params);
            console.log(params);
            axios.get(birdapi+urlapi,{
                params
            })
            .then((res) => {
                console.log(res);
                this.chatList.push({
                    answer:res.data.data.answer,
                    question:this.tInfo
                })
            })
        }
    },
}
</script>
<style scoped>
    .talk-list{
        font-size: .4rem;
    }
     .talk-list .as{
         text-align: left;
         background-color: greenyellow;
     }
     .talk-list .qu{
         text-align: right;
         background-color: greenyellow;
     }
     .talk-form{
        position: fixed;
        bottom: 50px;
        right: 10px;
     }
     .talk-test{
         position: absolute;
         bottom: 75px;
     }
</style>